	computed: {
		// 筛选数量已选
		selectArr() {
			var arr = [];
			this.mainArray.map((item, index) => {
				item.list.map((item1, index1) => {
					if (item1.num) {
						arr.push(item1);
					}
				})
			})
			return arr;
		},
		// 计算总价
		titleAll() {
			var all = 0;
			this.selectArr.map(item => {
